<template>
  <header class="px-2 py-1 bg-dark bg-gradient text-white qq ">
    <div
      class="
        d-flex
        flex-wrap
        align-items-center
        justify-content-center justify-content-lg-start
      "
    >
      <div
        class="mb-0 col-4 col-lg-auto me-lg-auto justify-content-center mb-md-0"
      >
        <div class="logowid nav-link p-0 text-secondary">
          <router-link to="/"
            ><img class="w-100" src="./logo.png" alt=""
          /></router-link>
        </div>
        <!-- <li><a href="#" class="nav-link px-2 text-white">Pricing</a></li>
        <li><a href="#" class="nav-link px-2 text-white">FAQs</a></li>
        <li><a href="#" class="nav-link px-2 text-white">About</a></li> -->
      </div>

      <div class="text-end col-8">
        <button
          type="button"
          class="btn me-2"
        >
          <router-link to="/"
            ><i class="fas fa-list-alt"></i
          ></router-link>
        </button>
       
        <button class="btn me-2">
          <router-link to="/admin"><i class="fas fa-user-cog"></i></router-link>
        </button>
         <button
          v-if="store.state.Islogin == false"
          type="button"
          class="btn me-2"
        >
          <router-link to="/login/"
            ><i class="fas fa-sign-in-alt"></i
          ></router-link>
        </button>
        <button
          @click="signout"
          v-if="store.state.Islogin == true"
          type="button"
          class="btn me-2"
        >
          <router-link to="/login/"
            ><i class="text-danger fas fa-sign-out-alt"></i
          ></router-link>
        </button>
        <div class="btn-group">
          <button
            type="button"
            class="btn me-2 btn text-white position-relative"
            data-bs-toggle="offcanvas"
            data-bs-target="#offcanvasScrolling"
            aria-controls="offcanvasScrolling"
            @click="getCart"
          >
            <i class="fas fa-shopping-cart"></i>
            <span
              class="
                cart-counts
                position-absolute
                start-100
                translate-middle
                badge
                rounded-circle
                bg-danger
              "
              >{{ this.store.state.carts }}</span
            >
          </button>
          <div
            class="offcanvas offcanvas-end"
            data-bs-scroll="true"
            data-bs-backdrop="false"
            tabindex="-1"
            id="offcanvasScrolling"
            aria-labelledby="offcanvasScrollingLabel"
            v-if="IsoffcanvasOpen"
          >
            <div class="offcanvas-header">
              <button
                type="button"
                class="btn-close text-reset"
                data-bs-dismiss="offcanvas"
                aria-label="Close"
              ></button>
            </div>
            <div class="offcanvas-body">
              <h5 class="text-start text-dark border-bottom pb-2">
                <i class="text-danger fas fa-shopping-cart me-2"></i>您的購物車
              </h5>
              <table class="table">
                <thead>
                  <th></th>
                  <th>品名</th>
                  <th>數量</th>
                  <th>單價</th>
                </thead>
                <tbody>
                  <tr v-for="item in cart.carts" :key="item.id">
                    <td class="align-middle">
                      <button
                        type="button"
                        class="btn btn-outline-danger btn-sm"
                        @click="removeCartItem(item.id)"
                      >
                        <i class="far fa-trash-alt"></i>
                      </button>
                    </td>
                    <td class="align-middle">
                      {{ item.product.title }}
                      <div class="text-success" v-if="item.coupon">
                        已套用優惠券
                      </div>
                    </td>
                    <td class="align-middle">
                      {{ item.qty }}/{{ item.product.unit }}
                    </td>
                    <td class="align-middle text-right">
                      {{ item.final_total }}
                    </td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td colspan="3" class="text-right">總計</td>
                    <td class="text-right">{{ cart.total }}</td>
                  </tr>
                  <tr v-if="cartSS.final_total != cart.total">
                    <td colspan="3" class="text-right text-success">折扣價</td>
                    <td class="text-right text-success">
                      {{ cart.final_total }}
                    </td>
                  </tr>
                </tfoot>
              </table>
              <div class="input-group mb-3 input-group-sm">
                <input
                  type="text"
                  class="form-control"
                  v-model="coupon_code"
                  placeholder="請輸入優惠碼"
                />
                <div class="input-group-append">
                  <button
                    class="btn btn-outline-secondary"
                    type="button"
                    @click="addCouponCode"
                  >
                    套用優惠碼
                  </button>
                </div>
              </div>
              <div class="my-5 row justify-content-center">
                <form class="col-12" @submit.prevent="createOrder">
                  <h3 class="text-start text-dark pb-2 border-bottom">
                    輸入訂購人資訊
                  </h3>
                  <div class="form-group text-start text-dark mb-2">
                    <label class="" for="useremail">Email</label>
                    <input
                      type="email"
                      class="form-control"
                      name="email"
                      id="useremail"
                      v-model="form.user.email"
                      placeholder="請輸入 Email"
                      required
                    />
                    <span class="text-danger"></span>
                  </div>

                  <div class="form-group text-start text-dark mb-2">
                    <label class="" for="username ">收件人姓名</label>
                    <input
                      type="text"
                      class="form-control"
                      name="name"
                      id="username"
                      v-model="form.user.name"
                      placeholder="輸入姓名"
                    />
                    <span class="text-danger"></span>
                  </div>

                  <div class="form-group text-start text-dark mb-2">
                    <label class="" for="usertel">收件人電話</label>
                    <input
                      type="tel"
                      class="form-control"
                      id="usertel"
                      v-model="form.user.tel"
                      placeholder="請輸入電話"
                    />
                  </div>

                  <div class="form-group text-start text-dark mb-2">
                    <label class="" for="useraddress">收件人地址</label>
                    <input
                      type="text"
                      class="form-control"
                      name="address"
                      id="useraddress"
                      v-model="form.user.address"
                      placeholder="請輸入地址"
                    />
                    <span class="text-danger">地址欄位不得留空</span>
                  </div>
                  <div class="form-group text-start text-dark">
                    <label for="comment">留言</label>
                    <textarea
                      name=""
                      id="comment"
                      class="form-control"
                      cols="30"
                      rows="10"
                      v-model="form.message"
                    ></textarea>
                  </div>
                  <div class="mt-3 text-right">
                    <button class="btn btn-danger" type="submit">
                      送出訂單
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import Loading from "@/main";
import { useStore } from "vuex";
export default {
  setup() {
    let store = useStore();
    return { store };
  },
  name: "NavBar",
  data() {
    return {
      Issignin: false,
      cart: [],
      cartSS: [],
      coupon_code: "",
      cart_count: 0,
      IsoffcanvasOpen: false,
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: "",
        },
        message: "",
      },
    };
  },
  components: {
    Loading,
  },
  methods: {
    signout() {
      let loader = this.$loading.show();
      const api = process.env.VUE_APP_SIGNOUT_API;
      const vm = this;
      this.axios.post(api, vm.user).then((res) => {
        console.log(res.data);
        loader.hide();
        if (res.data.success) {
          swal("已登出", "點擊OK關閉", "success");
          this.store.state.Islogin = false;
          vm.$router.push("/login");
        }
      });
    },
    getCart() {
      let vm = this;
      const api = `https://vue-course-api.hexschool.io/api/heather/cart`;
      this.axios.get(api).then((res) => {
        console.log(res);
        vm.cart = res.data.data;
        this.store.state.carts = vm.cart.carts.length;
        this.$data.IsoffcanvasOpen = true;
      });
    },
    removeCartItem(id) {
      let vm = this;
      let loader = this.$loading.show();
      const api = `https://vue-course-api.hexschool.io/api/heather/cart/${id}`;
      this.axios
        .delete(api)
        .then((res) => {
          loader.hide();
          swal({
            title: "刪除成功!",
            text: "1秒後自動關閉",
            timer: 1000,
            showConfirmButton: false,
          });
        })
        .then(() => {
          this.getCart();
        });
    },
    addCouponCode() {
      let vm = this;
      let loader = this.$loading.show();
      const api = `https://vue-course-api.hexschool.io/api/heather/coupon`;
      const coupon = {
        code: vm.coupon_code,
      };
      this.axios.post(api, { data: coupon }).then((res) => {
        console.log(res);
        vm.cartSS = res.data.data;
        this.getCart();
        loader.hide();
      });
    },
    createOrder() {
      let vm = this;
      let loader = this.$loading.show();
      const api = `https://vue-course-api.hexschool.io/api/heather/order`;
      const order = vm.form;
      this.axios
        .post(api, { data: order })
        .then((res) => {
          vm.$router.push(`/Customer_checkout/${res.data.orderId}`);
        })
        .then(() => {
          this.getCart();
          this.$data.IsoffcanvasOpen = false;
          loader.hide();
        });
    },
  },
  created() {
    const api2 = process.env.VUE_APP_USERCHECK_API;
    this.axios
      .post(api2)
      .then((res) => {
        if (res.data.success) {
          this.store.state.Islogin = true;
        } else {
          this.store.state.Islogin = false;
        }
      })
      .then(() => {
        this.getCart();
      });
  },
};
</script>


<style lang="scss">
.qq {
  position:fixed;
  z-index: 3000;
  top: 0;
  width: 100%;

}
.logowid {
  width: 40%;
}
.cart-counts {
  top: 17% !important;
  font-size: 10px;
}
</style>
