<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12 col-lg-12 mt-3">
        <div class="card ads d-flex flex-wrap" style='height:200px'>
          <img src="https://d.ecimg.tw/items/QEAN13A900ARQIH/000001_1598320835.jpg" alt="" class=' w-25 h-100'>
          <img src="https://upload.cc/i1/2021/08/09/mGvObS.png" class="Cloth d-md-block d-none " alt="">
          <p class="fs-3 ps-3  fw-bold text-start w-75">領取折扣，歡慶開幕 <br>
          <span class="fs-5 fw-bolder text-success">輸入優惠碼:</span> 
          <span class="fs-3 fw-bold text-danger">【{{code}}】</span><br class="d-block d-md-none">
          <span class="fs-5 fw-bolder text-success">即享9折優惠，<br class="d-block d-md-none">Aromatic 與您踏入馨香生活。</span></p>
        </div>
      </div>
       
      <div
        class="
          d-flex
          justify-content-between
          flex-wrap flex-md-nowrap
          align-items-center
          py-2
          mb-3
          border-bottom
        "
      >
        <h4 class="fs-4 m-0">探索商品</h4>
        <div class="btn-toolbar mb-2 mb-md-0">
          <button
            type="button"
            class="btn btn-outline-secondary"
            @click="WatchAll"
          >
            看全部
          </button>
          <div class="btn-group mx-2">
            <button
              type="button"
              class="btn btn-outline-primary dropdown-toggle"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              {{ selectCate }}
            </button>
            <ul class="dropdown-menu">
              <li
                v-for="item in filterCategory"
                :key="item"
                @click="filterCate(item.category)"
              >
                <a class="dropdown-item">{{ item.category }}</a>
              </li>
            </ul>
          </div>
          <div class="btn-group">
            <button
              type="button"
              class="btn btn-outline-success dropdown-toggle"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              {{ sortPrice }}
            </button>
            <ul class="dropdown-menu">
              <li>
                <a class="dropdown-item" @click="sortPriceHtoL">
                  <i class="me-2  fas fa-sort-numeric-down-alt"></i>
                  由高到低</a>
              </li>
              <li>
                <a class="dropdown-item" @click="sortPriceLtoH">
                  <i class="me-2 fas fa-sort-numeric-down"></i>
                  由低到高</a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <main role="main" class="col-md-12 ml-sm-auto col-lg-12 px-3">
        <div class="row">
          <div
            v-show="item.is_enabled == 1"
            v-for="(item, index) in products"
            :key="index"
            class="col-md-3 col-6 mb-2 p-2"
          >
            <div class="card">
              <div class="card-body p-1">
                <div class="imgarea" @click="getProduct(item.id)"><img class="w-100" :src="item.imageUrl" alt="" /></div>
                <h5 class="py-2 text-muted card-title">
                  {{ item.title }}
                </h5>
                <p class="card-text text-muted">
                  <span style="text-decoration: line-through"
                    >原價:{{ item.origin_price }}</span
                  >
                  <br />
                  <span class="fs-5 text-danger">特價:{{ item.price }}</span>
                </p>
                <div class="text-end">
                  <button
                    class="rounded-circle btn-outline-light btn text-danger"
                   @click="addtoCart(item.id)"
                  >
                    <i class="fas fa-heart"></i>
                  </button>
                  <button
                    @click="getProduct(item.id)"
                    class=" m-1 p-1  px-2 rounded-pill btn btn btn-outline-success"
                  >
                    詳細內容
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  </div>
</template> 

<script>
import axios from "axios";
import { useStore } from "vuex";
export default {
  name: "Info",
  setup() {
    let store = useStore();
    return { store };
  },
  data() {
    return {
      products: [],
      // product:{}
      filterCategory: [],
      selectCate: "選取分類",
      sortPrice: "價錢排序",
      code:'HELLOWORLD'
    };
  },
  created() {
    let loader = this.$loading.show();
    const api = "https://vue-course-api.hexschool.io/api/heather/products/all";
    axios.get(api).then((res) => {
     this.products = res.data.products;
    }).then(()=>{
      const set = new Set();
      let result = this.products.filter((item) =>
        !set.has(item.category) ? set.add(item.category) : false
      );
      this.filterCategory = result;
      loader.hide();
    });
  },
  methods: {
    getProduct(id) {
      let loader = this.$loading.show();
      let vm = this;
      const api = `https://vue-course-api.hexschool.io/api/heather/product/${id}`;
      axios.get(api).then((res) => {
        this.store.state.productId = id;
        vm.$router.push(`/product/${id}`);
        loader.hide();
        
      });
    },
    sortPriceHtoL() {
      let vm = this;
      this.products.sort(function (a, b) {
        vm.sortPrice = "由高到低";
        return b.price - a.price;
      });
    },
    sortPriceLtoH() {
      let vm = this;
      this.products.sort(function (a, b) {
        vm.sortPrice = "由低到高";
        return a.price - b.price;
      });
    },
    filterCate(e) {
      let loader = this.$loading.show();
      const api =
        "https://vue-course-api.hexschool.io/api/heather/products/all";
      axios.get(api).then((res) => {
        console.log(res);
        this.products = res.data.products;
        loader.hide();

        this.selectCate = e;

        let temp = this.products;
        return (this.products = temp.filter((x) => x.category == e));
      });
    },
    WatchAll() {
      let loader = this.$loading.show();
      const api =
        "https://vue-course-api.hexschool.io/api/heather/products/all";
      axios.get(api).then((res) => {
        console.log(res);
        this.products = res.data.products;
        loader.hide();
        this.selectCate = "選取分類";
        this.sortPrice = "價錢排序";
      });
    }, 
    addtoCart(id, qty = 1) {
      let loader = this.$loading.show();
      const vm = this;
      const api = "https://vue-course-api.hexschool.io/api/heather/cart";
      const cart = {
        product_id: id,
        qty,
      };
      this.axios.post(api, { data: cart }).then((res) => {
        console.log(res);
        loader.hide();
        swal("成功加入購物車", "點擊繼續選購","success");
        this.getCart()
      });
    },
    getCart() {
      let loader = this.$loading.show();
      let vm = this;
      const api = `https://vue-course-api.hexschool.io/api/heather/cart`;
      this.axios.get(api).then((res) => {
        console.log(res);
        vm.cart = res.data.data;
        this.store.state.carts=vm.cart.carts.length
        console.log(vm.cart.carts.length);
        loader.hide();
      });
    },
  },
};
</script>


<style scoped lang="scss">
.card-title {
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.ads{
  overflow: hidden;
  position: relative;
  border-radius:10px;
}
.ads p{
  position: absolute;
  top:30%;
  left:25%
}
.imgarea{
  overflow: hidden;
}
.imgarea img{
  transition: .3s;
}
.imgarea img:hover {
transform: scale(1.1);
}
.Cloth{
  width: 24% !important;
  position: absolute;
  top:-49%;
  left:78%;
  transform:rotate(18deg);
}
</style>